// library's
import React from 'react';
import { motion } from 'framer-motion';
import { useSelector } from 'react-redux';

// components
import { TextButton } from "../buttons/textButton/TextButton";

// state 
import { selectPrimaryColor } from '../../features/settings/appSettings/appearanceSettings/appearanceSettingsSlice';

// style 
import "./Error.css";

export const Error = ({errorMessage, action, buttonLabel = "Close"}) => {

    const primaryColor = useSelector(selectPrimaryColor)

    return (
        <motion.div className='error-container' initial={{scale: 0}} animate={{scale: 1}} exit={{scale: 0}} key={"error-componen"}> 
            <div className='inner-error-container' >
                <div
                style={{
                    backgroundColor: primaryColor
                }} 
                className='error-message-container'>
                    <p>{errorMessage}</p>
                </div>
                <TextButton action={action} name={buttonLabel} />
            </div>
        </motion.div>
    )
}
