
// library's
import { useAnimation, motion } from 'framer-motion'
import React from 'react'
import { useSelector } from 'react-redux';

// state
import {selectAccentColor, selectPrimaryColor, selectSecondaryColor } from '../../../features/settings/appSettings/appearanceSettings/appearanceSettingsSlice'

export const ButtonAnimationWrapper = ({action, className, width = 50, height = 50, borderRadius = '15px', justifyContent = 'center', invert = false, pointerOptions = null, children, active = false, opacity = 1}) => {

    const animation = useAnimation();

    const primaryColor = useSelector(selectPrimaryColor);

    const accentColor = useSelector(selectAccentColor);

    const secondaryColor = useSelector(selectSecondaryColor);

    const handleAnimation = (color, e) => {
        if (e) e.stopPropagation();
        if (active) return;
        animation.start({
            backgroundColor: color
        })
        
    }

    React.useEffect(() => {
        animation.start({
            backgroundColor: active && opacity === 1 ? accentColor : "rgba(255, 255, 255, 0)",
            opacity: active ? opacity : 1
        })
    }, [active])

    return (
        <motion.div
        onClick={action}
        className={className}
        style={{
            borderRadius: borderRadius,
            width: width,
            height: height,
            flexShrink: 0,
            cursor: active ? 'default' : 'pointer',
            display: 'flex',
            justifyContent: justifyContent,
            alignItems: 'center',
            padding: 10,
            pointerEvents: pointerOptions,
        }}
        animate={animation}
        onMouseEnter={(e) => {handleAnimation(!invert ? primaryColor : secondaryColor, e)}}
        onMouseLeave={(e) => {handleAnimation("rgba(255, 255, 255, 0)", e)}}
        onMouseDown={(e) => {handleAnimation(!invert ? accentColor : accentColor, e)}}
        onMouseUp={(e) => {handleAnimation(!invert ? primaryColor : secondaryColor, e)}}
        transition={{duration: 0.1}}
        >
            {children}
        </motion.div>
    )
}
