// library's
import { motion, useAnimation } from 'framer-motion';
import React from 'react';
import { useSelector } from 'react-redux';
import { useDropzone } from 'react-dropzone';

// state
import { selectAccentColor, selectPrimaryColor, selectTextColor } from '../../../features/settings/appSettings/appearanceSettings/appearanceSettingsSlice';
import { ImageButton } from './ImageButton/ImageButton';

// style
import "./MessageInput.css";
import { SendButton } from './SendButton/SendButton';
import { Image } from '../../Image/Image';

export const MessageInput = ({send, text, keyCode, active, image, value, persist}) => {

    const [files, setFiles] = React.useState([{}])

    const primaryColor = useSelector(selectPrimaryColor);
    
    const textColor = useSelector(selectTextColor);

    const accentColor = useSelector(selectAccentColor);

    const animation = useAnimation();

    const {getRootProps, getInputProps, isDragActive} = useDropzone({
        accept: {
            "image/*": ['.jpeg', '.png', '.webp', '.jpg']
        },
        maxFiles: 1,
        onDrop: acceptedFiles => {
            setFiles(acceptedFiles.map(file => Object.assign(file, {
                preview: URL.createObjectURL(file)
            })))
        }

    })

    React.useEffect(() => {
        
        if (files[0].size) image(files[0]);
        
        return () => files.forEach(file => URL.revokeObjectURL(file.preview));
    }, [files])

    const handleText = (e) => {
        text(e.target.value)
    }

    const handleKeyCode = (e) => {
        if (keyCode === false) {
            return
        }
        
        keyCode(e.keyCode)
    }

    const handleSend = () => {
        send();
    }

    return (
        <> 
            <motion.div 
            
            animate={animation}
            style={{
                backgroundColor: primaryColor
            }}
            className="message-input-container" >
                <input id='social-input-selector' onKeyUp={handleKeyCode} onChange={handleText} value={value}  placeholder='Message' type="text" />
                <div className='message-input-button-wrapper'>
                    {persist ? <ImageButton root={getRootProps({className: 'dropzone'})} /> : null}
                    <SendButton action={handleSend} />
                </div>
            </motion.div>
        </>
    )
}
